import React from 'react';
import Header from '../components/Header';
import Navbar from '../components/Navbar';

const style = {
  margin: '50px 0 0',
  // display: 'flex',
  // textAlign: 'center',
  // justifyContent: 'center',
};

function ErrorPageNotFound() {
  return (
    <>
      <Header />
      <Navbar />
      <div className="container">
        <h1 style={{ ...style }}> -   </h1>
      </div>
    </>
  );
}

export default ErrorPageNotFound;
