import React from 'react';
import cl from './PaidForm.module.css';

function PaidForm() {
  return (
    <form id="form" action="https://patriotlesnoy.ru/api/paidservice.php" method="POST">
      <div className={cl.block}>
        <label htmlFor="fullName">  :</label>
        <input
          className={`${cl.input} ${cl.inputM}`}
          type="text"
          name="fullName"
          id="fullName"
          placeholder="    "
          required
        />
      </div>

      <div className={cl.block}>
        <label htmlFor="phone">:</label>
        <input
          className={`${cl.input} ${cl.inputM}`}
          type="tel"
          name="phone"
          id="phone"
          placeholder="  "
          required
        />
      </div>

      <div className={cl.block}>
        <label htmlFor="dateEvent"> :</label>
        <input
          className={`${cl.input} ${cl.inputS}`}
          type="date"
          name="dateEvent"
          id="dateEvent"
          required
        />
      </div>

      <div style={{ display: 'flex' }}>
        <div className={cl.block}>
          <label htmlFor="timeEvent"> :</label>
          <input
            className={`${cl.input} ${cl.inputVS}`}
            type="time"
            name="timeEvent"
            id="timeEvent"
            min="09:00"
            max="18:00"
            required
          />
          <small className={cl.small}> 09:00  18:00</small>
        </div>

        <div className={cl.block} style={{ marginLeft: '2rem' }}>
          <label htmlFor="duration"></label>
          <select id="duration-list" className={`${cl.select} ${cl.inputVS}`}>
            <option defaultValue="1 ">1 </option>
            <option value="2 ">2 </option>
            <option value="3 ">3 </option>
            <option value="4 ">4 </option>
          </select>
          <small className={cl.small}> 1  4 </small>
        </div>
      </div>

      <div className={cl.block}>
        <label htmlFor="numberPlayers"> :</label>
        <input
          placeholder=" -"
          className={`${cl.input} ${cl.inputVS}`}
          type="number"
          name="numberPlayers"
          id="numberPlayers"
          required
        />
      </div>

      <div className={cl.block}>
          (14+)
        <div>
          <input
            type="radio"
            name="childrenWill"
            id="childrenWill-yes"
            required
          />
          <label htmlFor="childrenWill-yes"></label>
        </div>
        <div>
          <input
            type="radio"
            name="childrenWill"
            id="childrenWill-no"
          />
          <label htmlFor="childrenWill-no"></label>
        </div>
      </div>

      <div className={cl.blockCheckbox}>
        <input type="checkbox" name="rules" id="rules" required />
        <label htmlFor="rules" style={{ marginLeft: '1rem' }}>
          <a className="ref" href="">     </a>
        </label>
      </div>

      <div className={cl.blockCheckbox}>
        <input type="checkbox" name="dataProcessing" id="dataProcessing" required />
        <label htmlFor="dataProcessing" style={{ marginLeft: '1rem' }}>    </label>
      </div>

      <div style={{ width: '100%', textAlign: 'center' }}>
        <button className={cl.btn} type="submit"> </button>
      </div>
    </form>
  );
}

export default PaidForm;
